  .info__bottom {
    gap: 25px;
  }
}
@media (max-width: 767px) {
  .info__bottom {
    gap: 10px;
  }
}

.info__bottom span {
  font-size: 13px;
  font-weight: 700;
  line-height: 13px;
  letter-spacing: 0;
  text-align: left;
}
@media (max-width: 767px) {
  .info__bottom span {
    font-size: 12px;
    line-height: 12px;
  }
}

.info__bottom p {
  margin: 0;
}

.info__level {
  width: 81px;
}

.info__level span {
  font-size: 12px;
  font-weight: 300;
  line-height: 12px;
  letter-spacing: 0;
  text-align: left;
  position: relative;
  top: -20px;
}

.info__level::after {
  content: url("../img/sprite/two-stars.svg");
  position: relative;
  top: -20px;
}

.info__level::before {
  content: url("../img/sprite/line.svg");
  color: #ffffff;
  position: relative;
  top: -30px;
}
@media (max-width: 767px) {
  .info__level::before {
    top: -25px;
  }
}

.info__level-three::after {
  content: url("../img/sprite/three-stars.svg");
  position: relative;
  top: -20px;
}

